// import ReactTooltip from 'react-tooltip'
import { Icon } from '@iconify/react'
import { usePopperTooltip } from 'react-popper-tooltip'
import 'react-popper-tooltip/dist/styles.css'
import styles from './technology.module.scss'
import classNames from 'classnames'

interface TechnologyProps {
    readonly name: string
    readonly description?: string
    readonly level?: number
    readonly color?: string
    readonly icon?: string
    readonly iconColor?: string
    readonly img?: string
}
function Technology({ name, description, icon, iconColor, img, color: _ }: TechnologyProps) {
    const { getArrowProps, getTooltipProps, setTooltipRef, setTriggerRef, visible } =
        usePopperTooltip()

    return (
        <>
            <span
                // style={{ backgroundColor: color }}
                ref={setTriggerRef}
                className={styles.Technology}
                data-tip={description}
            >
                {icon && <Icon fontSize={18} icon={icon} color={iconColor} />}
                {img && <img src={img} alt={description ?? name} style={{ height: '18px' }} />}
                {(icon || img) && <> </>}
                {name}
            </span>
            {visible && (
                <div
                    ref={setTooltipRef}
                    {...getTooltipProps({
                        className: classNames('tooltip-container', styles.TechnologyTooltip),
                    })}
                >
                    <div
                        {...getArrowProps({
                            className: classNames('tooltip-arrow', styles.TechnologyTooltipArrow),
                        })}
                    />
                    {description}
                </div>
            )}
        </>
    )
}

export default Technology
