import SkillsListItem from './skills-list-item'
import styles from './skills-list.module.scss'

export interface SkillInfo {
    readonly name?: string | JSX.Element
    readonly description?: string | JSX.Element
    readonly level?: number
    readonly children?: SkillInfo[]
}

interface SkillsListProps {
    readonly title: string
    readonly items: SkillInfo[]
}

function SkillsList({ title, items }: SkillsListProps) {
    return (
        <>
            <h2>{title}</h2>
            <ul className={styles.SkillsList}>
                {items.map(item => (
                    <SkillsListItem item={item} key={`${item.name}/${item.description}`} />
                ))}
            </ul>
        </>
    )
}

export default SkillsList
