import { NavLink } from 'react-router-dom'
import styles from './menu.module.scss'
import Menu, { MenuItemModel } from './menu'
import './menu.scss'

interface MenuItemProps {
    readonly item: MenuItemModel
}

function MenuItem({ item }: MenuItemProps) {
    const { to, title, children } = item

    let renderedItem = <>{title}</>

    if (to) {
        renderedItem = (
            <NavLink to={to ?? ''} className={styles.MenuItemLink}>
                {renderedItem}
            </NavLink>
        )
    }

    return (
        <>
            {renderedItem}
            {children && <Menu items={children} />}
        </>
    )
}

export default MenuItem
