import styles from './skills-level.module.scss'
import classNames from 'classnames'

interface SkillsLevelProps {
    readonly level?: number
}

function SkillsLevel({ level }: SkillsLevelProps) {
    if (!level) {
        return <></>
    }
    return (
        <div className={styles.SkillsLevel}>
            {Array(10)
                .fill(0)
                .map((_, i) => (
                    <div
                        className={classNames(styles.SkillsLevelMarker, {
                            [styles.SkillsLevelMarkerEmpty]: i >= level,
                        })}
                        key={i}
                    />
                ))}
        </div>
    )
}

export default SkillsLevel
