import { SkillInfo } from './skills-list'
import styles from './skills-list.module.scss'
import classNames from 'classnames'
import SkillsLevel from '../SkillsLevel'

interface SkillsListItemProps {
    readonly item: SkillInfo
}

function SkillsListItem({ item }: SkillsListItemProps) {
    const { name, description, children } = item

    const text = (
        <>
            {name && <strong>{name}: </strong>}
            {description}
        </>
    )

    if ((children?.length ?? 0) > 0) {
        return (
            <li className={styles.SkillsListItem}>
                {text}
                <ul className={styles.SkillsList}>
                    {children?.map(child => (
                        <SkillsListItem item={child} key={`${child.name}/${child.description}`} />
                    ))}
                </ul>
            </li>
        )
    } else {
        return (
            <li className={classNames(styles.SkillsListItem, styles.SkillsListItemHoverable)}>
                <div
                    className={classNames({
                        [styles.SkillsListWithLevel]: item.level !== undefined,
                    })}
                >
                    <div>{text}</div>
                    <SkillsLevel level={item.level} />
                </div>
            </li>
        )
    }
}

export default SkillsListItem
